.formRoot {
    background-color: cornflowerblue;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
}


form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

label {
    width: 100px;
    display: block;
    float: left;
    text-align: left;
}

textarea {
  resize: none;
}

textarea, input {
    width: 300px
}

@media screen and (width<400px) {
    textarea, input {
        width: calc(100% - 10px);
    }

    label {
        width: 70px
    }
}


.error {
    color: rgb(55, 13, 13);
    &::before {
        content: 'âŒ';
    }
}

.response, form {
    border: 1px solid black;
    width: fit-content;
    padding: 5px;
    margin: 5px;
    background-color: rgb(132, 166, 230);
    border-radius: 5px;
    box-shadow: 5px 5px black;
}

.links {
    list-style: none;
    padding-left: 0;
}